import React, { useState, useRef, useEffect } from 'react';
import PropTypes from 'prop-types';

import {
  Box, Container, FormControl, Input, InputLabel, FormHelperText,
  Paper, Stack, FormGroup, Button, Typography
} from '@mui/material';

import { camelCaseProp, TIMER_DEFAULTS } from '../data/TimerSlice';

const TimeInput = ({ value, ...props }) => (
  <Input
    {...props}
    variant="filled"
    type="number"
    defaultValue={value}
    inputProps={{ style: { textAlign: 'center' } }}
  />
);
TimeInput.propTypes = {
  value: PropTypes.number.isRequired
};

/**
 * The Structure of the Form State
 * @typedef {Object} FormState
 * @property {String} timerName - the name of the timer
 * @property {Number} hours -
 * @property {Number} minutes -
 * @property {Seconds} seconds -
 */

/**
 * Form for submitting timers
 * @param {{ theme: String, handleCancel:Function, handleSubmit:Function}} params -
 * @returns {Element}
 */
const TimerForm = ({
  theme,
  uniqueKey,
  timerName,
  hours,
  minutes,
  seconds,
  handleCancel,
  handleSubmit
}) => {
  //   <Stack direction="column" spacing={1}>

  /**
   * Form State
   * @type {[ FormState, Function ]}
   */
  const [formState, setFormState] = useState({
    uniqueKey,
    timerName,
    hours,
    minutes,
    seconds
  });

  /**
   * Ref used to interact with the timerName input
   */
  const timerNameRef = useRef(null);

  /**
   * List of errors
   * @type {[String[], Function]}
   */
  const [errors, setErrors] = useState([]);

  /**
   * Validates the form
   * @param {FormState} formState -
   * @returns {String[]} - collection of errors
   */
  const validateForm = ({ timerName: timerNameVal, ...rest }) => {
    let currentErrors = [];

    if (timerNameVal === 'cuca') {
      currentErrors = [...currentErrors, 'Timer Name cannot be cuca'];

      //-- @TODO: doesn't seem to do anything
      timerNameRef.current.error = true;
    } else {
      timerNameRef.current.error = false;
    }

    if (rest.hours + rest.minutes + rest.seconds === 0) {
      currentErrors = [...currentErrors, 'Please specify a duration'];
    }

    return currentErrors;
  };

  /**
   * Validates the submit
   * @param {Event} evt - submit event
   */
  const validateSubmit = (evt) => {
    evt.preventDefault();
    evt.stopPropagation();

    const errorsFound = validateForm(formState);
    setErrors(errorsFound);

    if (errorsFound && errorsFound.length > 0) {
      // console.error('there are errors');
    } else {
      handleSubmit(formState);
      // console.log('NO ERRORS!');
    }
  };

  /**
   * Handles an change in one of the inputs.
   * @param {Event} evt - change event
   */
  const handleChange = (evt) => {
    // console.log('changeHappened');
    let { name, value } = evt.target;

    //-- camel case the property, like timer-name to timerName
    name = camelCaseProp(name);

    //-- converts the value to a number if that is the type
    if (evt.target.type === 'number') {
      try {
        value = parseInt(value, 10);
      } catch (err) {
        value = 0;
      }
    }
    const newFormState = {
      ...formState,
      [name]: value
    };
    setFormState(newFormState);
  };

  //-- clear form - use refs

  return (
    <Container maxWidth="sm" className="timer-form" sx={{ mt: 2 }}>
      <Paper>
        <form onSubmit={validateSubmit}>
          <Stack direction="column" spacing={3} p={3} alignItems="center">
            <Box>
              <Typography variant="body1">{errors}</Typography>
            </Box>
            <FormControl fullWidth>
              <InputLabel htmlFor="input-timer-name">Timer Name</InputLabel>
              <Input
                id="input-timer-name"
                name="timer-name"
                variant="filled"
                aria-describedby="helper-timer-name"
                onChange={handleChange}
                ref={timerNameRef}
                value={formState.timerName}
                required
              />
              <FormHelperText id="helper-timer-name">Name for the timer</FormHelperText>
            </FormControl>
            <Stack direction="row">
              <FormControl>
                <InputLabel htmlFor="input-hours">Hours</InputLabel>
                <TimeInput id="input-hours" name="hours" value={formState.hours} onChange={handleChange} className="number-input" />
              </FormControl>
              <span>:</span>
              <FormControl>
                <InputLabel htmlFor="input-minutes">Minutes</InputLabel>
                <TimeInput id="input-minutes" name="minutes" value={formState.minutes} onChange={handleChange} />
              </FormControl>
              <span>:</span>
              <FormControl>
                <InputLabel htmlFor="input-seconds">Seconds</InputLabel>
                <TimeInput id="input-seconds" name="seconds" value={formState.seconds} onChange={handleChange} />
              </FormControl>
            </Stack>
            <Stack
              direction="row"
              alignItems="center"
              justifyContent="space-around"
              sx={{ width: '100%' }}
              spacing={1}
            >
              <Button
                variant="outlined"
                onClick={handleCancel}
              >
                Cancel
              </Button>
              <Button
                variant="contained"
                type="submit"
              >
                Submit
              </Button>
            </Stack>
          </Stack>
        </form>
      </Paper>
    </Container>
  );
};

TimerForm.defaultProps = {
  theme: 'primary',
  uniqueKey: '',
  timerName: '',
  hours: 0,
  minutes: 0,
  seconds: 0,
  handleCancel: () => {},
  handleSubmit: () => {}
};

TimerForm.propTypes = {
  /**
   * Description
   */
  theme: PropTypes.oneOf(['primary', 'secondary']),

  uniqueKey: PropTypes.string,
  timerName: PropTypes.string,

  hours: PropTypes.number,
  minutes: PropTypes.number,
  seconds: PropTypes.number,

  handleCancel: PropTypes.func,
  handleSubmit: PropTypes.func
};

export default TimerForm;
