import React from 'react';
import PropTypes from 'prop-types';

import {
  Box, Button
} from '@mui/material';

const AddTimerButton = ({ theme, handleAddTimer }) => {
  return (
    <Button
      variant="outlined"
      onClick={handleAddTimer}
      sx={{ width: '100%', height: 40, backgroundColor: 'white' }}
    >
      +
    </Button>
  );
};

AddTimerButton.defaultProps = {
  theme: 'primary',
  handleAddTimer: () => {}
};

AddTimerButton.propTypes = {
  /**
   * Description
   */
  theme: PropTypes.oneOf(['primary', 'secondary']),

  handleAddTimer: PropTypes.func
};

export default AddTimerButton;
