import React from 'react';
import PropTypes from 'prop-types';

import {
  Box, Paper, Grid, Stack, Button, ButtonGroup,
  LinearProgress, LinearProgressWithLabel, CircularProgress,
  Typography
} from '@mui/material';

import * as TimerSlice from '../data/TimerSlice';

const timePad = (num) => TimerSlice.zeroFill(num, 2);

const TimerEntry = ({
  theme,
  uniqueKey,
  timerName,
  hours,
  minutes,
  seconds,
  startDate,
  progressPct,
  handleRemove,
  handleEdit,
  handleUp,
  handleDown
}) => {
  const startTimeValue = TimerSlice.checkDate(startDate);
  const roundProgressPct = progressPct ? Math.round(progressPct) : 0;
  const newEndTime = TimerSlice.addDate(
    startDate,
    0,
    hours,
    minutes,
    seconds
  );
  const { rangeStart, rangeEnd } = TimerSlice.formatDateRange(startTimeValue, newEndTime);
  const elapsedDuration = TimerSlice.formatDurationPercentage(
    TimerSlice.timeDuration(0, hours, minutes, seconds),
    progressPct
  );
  return (
    <Paper>
      <Box className="timer-entry" p={2}>
        <Stack
          className="timer-title"
          direction="row"
          alignItems="center"
          justifyContent="space-between"
        >
          <Typography variant="h4" component="span" className="duration">
            {timePad(hours)}:{timePad(minutes)}:{timePad(seconds)}
          </Typography>
          <Typography variant="h5" component="span" className="title">
            {timerName}
          </Typography>
        </Stack>
        <div className="Progress">
          {
            progressPct <= 0
              ? <span>Not started</span>
              : (
                <div style={{ position: 'relative', textAlign: 'left' }}>
                  {
                    progressPct >= 100
                      ? <span />
                      : (
                        <span className="progress-duration" style={{ position: 'absolute', right: 0 }}>
                          Elapsed: {timePad(elapsedDuration.hours)}:{timePad(elapsedDuration.minutes)}:{timePad(elapsedDuration.seconds)}
                        </span>
                      )
                  }
                  <span className="progress-pct">
                    Progress: {roundProgressPct}%
                  </span>
                </div>
              )
          }
          <LinearProgress
            variant="determinate"
            value={progressPct}
          />
        </div>
        <div className="time-range">
          {
            !startTimeValue ? ''
              : (
                <span className="range-start">
                  {rangeStart}
                </span>
              )
          }
          <span className="range-separator"> - </span>
          {
            !newEndTime ? ''
              : (
                <span className="range-end">
                  {rangeEnd}
                </span>
              )
          }
        </div>
        <div className="actions">
          <Stack
            direction="row"
            alignItems="center"
            justifyContent="space-around"
            sx={{ width: '100%' }}
            mt={2}
          >
            <Button
              variant="contained"
              onClick={() => handleRemove({ uniqueKey, timerName })}
            >
              Remove
            </Button>
            <Button
              variant="contained"
              onClick={() => handleEdit({ uniqueKey, timerName })}
            >
              Edit
            </Button>
            <ButtonGroup
              variant="outlined"
            >
              <Button onClick={() => handleUp({ uniqueKey, timerName })}>Up</Button>
              <Button onClick={() => handleDown({ uniqueKey, timerName })}>Down</Button>
            </ButtonGroup>
          </Stack>
        </div>
      </Box>
    </Paper>
  );
};

TimerEntry.defaultProps = {
  theme: 'primary',
  uniqueKey: '1',
  timerName: 'Timer 1',
  startDate: new Date(),
  progressPct: 0,
  hours: 0,
  minutes: 0,
  seconds: 0,

  handleRemove: () => {},
  handleEdit: () => {},
  handleUp: () => {},
  handleDown: () => {}
};

TimerEntry.propTypes = {
  /**
   * Description
   */
  theme: PropTypes.oneOf(['primary', 'secondary']),

  uniqueKey: PropTypes.oneOfType([
    PropTypes.number,
    PropTypes.string
  ]),

  hours: PropTypes.number,
  minutes: PropTypes.number,
  seconds: PropTypes.number,

  timerName: PropTypes.string,

  progressPct: PropTypes.number,

  startDate: PropTypes.oneOfType([
    PropTypes.instanceOf(Date),
    PropTypes.number,
    PropTypes.string
  ]),

  handleRemove: PropTypes.func,
  handleEdit: PropTypes.func,
  handleUp: PropTypes.func,
  handleDown: PropTypes.func
};

export default TimerEntry;
