/**
 * Label Value
 */
class LabelValue {
  /** @type {String} */
  label;

  /** @type {any} */
  value;

  /**
   * Constructor
   * @param {string} label -
   * @param {string} value -
   */
  constructor(label, value) {
    this.label = label;
    this.value = value;
  }
}
export default LabelValue;
