/* eslint-disable no-nested-ternary */
import React from 'react';
import PropTypes from 'prop-types';

import {
  Stack, Paper, Box
} from '@mui/material';

import * as TimerSlice from '../data/TimerSlice';
import TimerEntry from './TimerEntry';
import TimerPlaceholder from './TimerPlaceholder';
import AddTimerButton from './AddTimerButton';
import Timer from '../data/Timer';

/** @type {[import('../data/TimerSlice') */

/**
 * Component
 */
const TimerList = ({
  theme,
  timerMode,
  startDate,
  currentDate,
  isRunning,
  timers,
  handleAddTimer,
  handleEditTimer,
  handleRemoveTimer,
  handleTimerUp,
  handleTimerDown
}) => {
  /** @type {TimerSlice.Duration} */
  const timeDifference = TimerSlice.timeDifference(
    startDate,
    currentDate
  );

  const startDateStr = TimerSlice.dateToString(startDate);
  const currentDateStr = TimerSlice.dateToString(currentDate);

  const hasTimers = Array.isArray(timers) && timers.length > 0;

  const showPlaceholder = timerMode !== TimerSlice.TIMER_MODES.START_IMMEDIATELY.value;

  // <div>{startDateStr}</div>
  // <div>{currentDateStr}</div>

  return (
    <Box>
      <Stack
        layout="column"
        spacing={2}
      >
        {
          hasTimers && showPlaceholder
          && (
            <TimerPlaceholder
              days={timeDifference.days}
              hours={timeDifference.hours}
              minutes={timeDifference.minutes}
              seconds={timeDifference.seconds}
              elapsed={timeDifference.elapsed}
            />
          )
        }
        {
          (!timers) ? ''
            : timers.map(
              /**
               * @param {Timer} timer -
               */
              (timer) => (
                <TimerEntry
                  key={timer.uniqueKey}
                  uniqueKey={timer.uniqueKey}
                  timerName={timer.timerName}
                  startDate={timer.startDate}
                  hours={timer.hours}
                  minutes={timer.minutes}
                  seconds={timer.seconds}
                  progressPct={timer.progressPct}
                  handleEdit={handleEditTimer}
                  handleRemove={handleRemoveTimer}
                  handleUp={handleTimerUp}
                  handleDown={handleTimerDown}
                />
              )
            )
          }
        <AddTimerButton
          handleAddTimer={handleAddTimer}
        />
      </Stack>
    </Box>
  );
};

TimerList.defaultProps = {
  theme: 'primary',

  timerMode: 'start-immediately',
  startDate: new Date(),
  currentDate: new Date(),

  isRunning: false,

  timers: [],

  handleAddTimer: () => {},
  handleEditTimer: () => {},
  handleRemoveTimer: () => {},
  handleTimerUp: () => {},
  handleTimerDown: () => {}
};

TimerList.propTypes = {
  /**
   * Description
   */
  theme: PropTypes.oneOf(['primary', 'secondary']),

  timerMode: PropTypes.oneOf([
    'start-immediately',
    'start-at-time',
    'end-at-time'
  ]),

  isRunning: PropTypes.bool,

  startDate: PropTypes.oneOfType([
    PropTypes.instanceOf(Date),
    PropTypes.number,
    PropTypes.string
  ]),

  currentDate: PropTypes.oneOfType([
    PropTypes.instanceOf(Date),
    PropTypes.number,
    PropTypes.string
  ]),

  timers: PropTypes.arrayOf(PropTypes.oneOfType([
    PropTypes.instanceOf(Timer),
    PropTypes.object
  ])),

  handleAddTimer: PropTypes.func,
  handleEditTimer: PropTypes.func,
  handleRemoveTimer: PropTypes.func,
  handleTimerUp: PropTypes.func,
  handleTimerDown: PropTypes.func
};

export default TimerList;
