import { React, StrictMode, useRef, useEffect } from 'react';

import { Box, Stack, Paper, Container, Modal, Typography } from '@mui/material';

import { useDispatch, useSelector } from 'react-redux';

import * as TimerSlice from './data/TimerSlice';

import Header from './components/Header';
import MainSettings from './components/MainSettings';
import TimerList from './components/TimerList';
import TimerActions from './components/TimerActions';
import TimerForm from './components/TimerForm';
import Sharable from './components/Sharable';

import logo from './logo.svg';
import './App.css';

const App = () => {
  const dispatch = useDispatch();
  const appRef = useRef(null);

  const timerStore = useSelector(TimerSlice.selectorTimerSlice);
  const isRunning = useSelector(TimerSlice.selectorIsRunning);
  const lastTickDate = useSelector(TimerSlice.selectorLastTickDate);

  const {
    stopwatch: {
      currentTime,
      tickDuration,
      tickDurationStr
    }
  } = timerStore;

  const startDate = TimerSlice.checkDate(timerStore.settings.startDate);
  const timerEditState = useSelector(TimerSlice.selectorEditTimerForm);

  const emptyFn = (msg) => () => {
    console.log(msg);
  };

  const handleModeChanged = ({ target: { value: newMode } }) => {
    // console.log('mode changed', evt);
    dispatch(TimerSlice.setMode(newMode));

    if (newMode === TimerSlice.TIMER_MODES.START_IMMEDIATELY.value) {
      dispatch(TimerSlice.stopTimer({}));
    } else if (
      newMode === TimerSlice.TIMER_MODES.START_AT_TIME.value
      || newMode === TimerSlice.TIMER_MODES.END_AT_TIME.value
    ) {
      dispatch(TimerSlice.startTimer({}));
    }
  };

  const handleStartChanged = (newStartTime) => {
    newStartTime.setSeconds(0);
    newStartTime.setMilliseconds(0);
    // console.log('start time changed', evt);
    const startTimeSerialized = TimerSlice.serializeDate(newStartTime);

    //-- only proceed if the start time is a valid date.
    if (!startTimeSerialized) return;
    dispatch(TimerSlice.setStartDate(startTimeSerialized));
  };

  const handleStartStopToggle = () => {
    // console.log(`start stop clicked, [isRunning:${isRunning}]`);
    if (isRunning) {
      dispatch(TimerSlice.stopTimer({}));
    } else {
      dispatch(TimerSlice.startTimer({}));

      const {
        settings: {
          mode: timerMode
        },
        timers
      } = timerStore;

      if (timerMode === TimerSlice.TIMER_MODES.START_IMMEDIATELY.value) {
        const fullDuration = TimerSlice.totalDuration(timers);
        const targetEpoch = new Date().getTime() - tickDuration;
        const targetDate = new Date(targetEpoch);
        dispatch(TimerSlice.setStartDate(TimerSlice.serializeDate(targetDate)));
      }
    }
  };

  const handleReset = () => {
    // console.log('reset clicked');
    dispatch(TimerSlice.resetTimer());
  };

  //-- deprecated
  const handleAddTimerDirect = () => {
    const payload = {
      timerName: 'Intro',
      startDate: '2022-01-17T18: 29: 34.088Z',
      hours: 0,
      minutes: 20,
      seconds: 0
    };
    dispatch(TimerSlice.addTimer(payload));
  };

  const handleAddTimer = () => {
    const { uniqueKey, timerName, hours, minutes, seconds } = TimerSlice.TIMER_DEFAULTS;
    dispatch(TimerSlice.openEditTimer({ uniqueKey, timerName, hours, minutes, seconds }));
  };

  const handleRemoveTimer = ({ uniqueKey }) => {
    dispatch(TimerSlice.removeTimer(uniqueKey));
  };
  const handleEditTimer = ({ uniqueKey }) => {
    dispatch(TimerSlice.editTimer(uniqueKey));
  };

  const handleTimerUp = ({ uniqueKey }) => {
    dispatch(TimerSlice.moveTimerUp(uniqueKey));
  };
  const handleTimerDown = ({ uniqueKey }) => {
    dispatch(TimerSlice.moveTimerDown(uniqueKey));
  };

  const handleEditTimerSubmit = (formPayload) => {
    // console.log('editTimer called', formPayload);
    const { uniqueKey, timerName, hours, minutes, seconds } = formPayload;
    const startDateStr = TimerSlice.serializeDate(startDate);
    const payload = {
      uniqueKey,
      timerName,
      startDate: startDateStr,
      hours,
      minutes,
      seconds
    };
    if (uniqueKey) {
      dispatch(TimerSlice.updateTimer(payload));
    } else {
      dispatch(TimerSlice.addTimer(payload));
    }
    dispatch(TimerSlice.closeEditTimer({}));
  };
  const handleEditTimerCancel = () => {
    dispatch(TimerSlice.closeEditTimer());
  };

  const handleShare = () => {
    dispatch(TimerSlice.shareURL());
  };

  /*
  <div>
    <Typography variant="h5" component="div">
      Current Duration: {tickDurationStr}
    </Typography>
    <Typography variant="h5" component="div">
      Duration Milli: {tickDuration}
    </Typography>
  </div>
  */

  return (
    <Container maxWidth="sm" className="App" ref={appRef}>
      <Modal
        open={timerStore.editTimer.isOpen}
      >
        <Box>
          <TimerForm
            uniqueKey={timerEditState.uniqueKey}
            timerName={timerEditState.timerName}
            hours={timerEditState.hours}
            minutes={timerEditState.minutes}
            seconds={timerEditState.seconds}
            handleSubmit={handleEditTimerSubmit}
            handleCancel={handleEditTimerCancel}
          />
        </Box>
      </Modal>
      <Paper>
        <Stack spacing={2} p={2} mt={2}>
          <Header />
          <Sharable
            theme="outlined"
            onClick={handleShare}
          />
          <MainSettings
            handleModeChanged={handleModeChanged}
            handleStartChanged={handleStartChanged}
            startDate={startDate}
            timerMode={timerStore.settings.mode}
          />
          <TimerList
            handleAddTimer={handleAddTimer}
            handleEditTimer={handleEditTimer}
            handleRemoveTimer={handleRemoveTimer}
            handleTimerUp={handleTimerUp}
            handleTimerDown={handleTimerDown}
            isRunning={isRunning}
            timerMode={timerStore.settings.mode}
            startDate={startDate}
            currentDate={lastTickDate}
            timers={timerStore.timers}
          />
          <TimerActions
            isRunning={isRunning}
            handleStartStop={handleStartStopToggle}
            handleReset={handleReset}
          />
        </Stack>
      </Paper>
    </Container>
  );
};
export default App;
