import React from 'react';
import PropTypes from 'prop-types';

import {
  Box, Button
} from '@mui/material';

import LabelValue from '../data/LabelValue';

export const THEMES = {
  contained: new LabelValue('Contained', 'contained'),
  outlined: new LabelValue('Outlined', 'outlined'),
  text: new LabelValue('Text', 'text')
};

const Sharable = ({ theme, onClick }) => {
  return (
    <div>
      <Button
        variant={theme}
        onClick={onClick}
      >
        Update Sharable URL
      </Button>
    </div>
  );
};

Sharable.defaultProps = {
  theme: 'outlined',
  onClick: () => {}
};

Sharable.propTypes = {
  /**
   * Theme for the button
   */
  theme: PropTypes.oneOf(['contained', 'outlined', 'text']),

  /** Action when the button is clicked */
  onClick: PropTypes.func
};

export default Sharable;
