import React from 'react';
import PropTypes from 'prop-types';

import {
  Box,
  Paper,
  Typography
} from '@mui/material';

const Header = ({ theme, variant }) => {
  return (
    <Paper sx={{ padding: 1 }}>
      <Typography variant={variant} component="h1">
        Meeting Timer
      </Typography>
    </Paper>
  );
};

Header.defaultProps = {
  theme: 'primary',
  variant: 'h4'
};

Header.propTypes = {
  /**
   * Description
   */
  theme: PropTypes.oneOf(['primary', 'secondary']),

  /**
   * The variant size of the header
   */
  variant: PropTypes.oneOf(['h1', 'h2', 'h3', 'h4', 'h5', 'h6', 'h7'])
};

export default Header;
