/**
 * camel cases dashed property names
 * @param {string} dashedProperty -
 * @returns {String} - the property in camelCase
 */
export const camelCaseProp = (dashedProperty) => {
  if (!dashedProperty) return dashedProperty;

  return dashedProperty.replace(/-(\w)/g, (match, p1) => p1.toUpperCase());
};

/**
 * Serializes a date into a string
 * @param {Date} date -
 * @returns {string}
 */
export const serializeDate = (date) => {
  // eslint-disable-next-line no-restricted-globals
  if (date instanceof Date && !isNaN(date)) {
    return date.toISOString();
  }
  return null;
};

/**
 * Deserializes a date from a string
 * @param {string} str -
 * @returns {Date}
 */
export const deserializeDate = (str) => (str ? new Date(Date.parse(str)) : null);

/**
 * Ensures a date is represented as a string
 * @param {String|Date} dateVal - Representation of the date
 * @param {Boolean} useNowAsDefault - whether to use now as the default
 */
export const dateToString = (dateVal, useNowAsDefault = true) => {
  if (!dateVal) {
    if (useNowAsDefault) {
      return serializeDate(new Date());
    } else {
      return '';
    }
  } else if ((dateVal instanceof Date)) {
    return serializeDate(dateVal);
  }
  return `${dateVal}`;
};

/**
 * Formats a date to be legible
 * @param {Date} date -
 * @returns {String}
 */
export const formatDate = (date) => date?.toLocaleString();

/**
 * Converts a date string, number, instance to always be a date
 * @param {(Date|Number|String)} date -
 * @returns {Date}
 */
export const checkDate = (date) => {
  if (!date) {
    return null;
  }

  if (date instanceof Date) {
    return date;
  }
  if (date instanceof String) {
    return Date.parse(date);
  }

  return new Date(date);
};

/**
 * @typedef {Object} formatDateRange_Result
 * @property {String} rangeStart - locally formatted date + time
 * @property {String} rangeEnd - locally formatted date (if different) + time
 */

/**
 * Formats the time between two dates
 * (and collapses the year/month/day if they are the same)
 * @param {Date} d1 -
 * @param {Date} d2 -
 * @returns {formatDateRange_Result} - true if they are the same date
 */
export const formatDateRange = (d1, d2) => {
  if (!d1 || !d2) return false;

  const d1Date = d1.toLocaleDateString();
  const d1Time = d1.toLocaleTimeString();
  const d2Date = d2.toLocaleDateString();
  const d2Time = d2.toLocaleTimeString();

  const rangeStart = `${d1Date}, ${d1Time}`;
  const rangeEnd = (d1Date === d2Date)
    ? `${d2Time}`
    : `${d2Date}, ${d2Time}`;

  return { rangeStart, rangeEnd };
};

/**
 * @typedef {Object} DurationSimple
 * @property {Number} days -
 * @property {Number} hours -
 * @property {Number} minutes -
 * @property {Number} seconds -
 * @property {Number} milli -
 * @property {Number} epoch -
 */

/**
 * Determines the breakdown of time spent
 * @param {Number} duration - milliseconds duration
 * @param {Number} percentComplete - percentComplete/100
 * @returns {DurationSimple} - hours, minutes, seconds, etc that elapsed
 */
export const formatDurationPercentage = (duration, percentComplete) => {
  const result = { days: 0, hours: 0, minutes: 0, seconds: 0, milli: 0, epoch: 0 };

  result.epoch = Math.round(duration * (percentComplete / 100));

  //-- shortcut
  if (percentComplete === 100) return result;

  if (result.epoch === 0) return result;

  let div = divideRemainders(result.epoch, TIME_OFFSET.DAYS);
  result.days = div.divisible;
  div = divideRemainders(div.remainder, TIME_OFFSET.HOURS);
  result.hours = div.divisible;
  div = divideRemainders(div.remainder, TIME_OFFSET.MINUTES);
  result.minutes = div.divisible;
  div = divideRemainders(div.remainder, TIME_OFFSET.SECONDS);
  result.seconds = div.divisible;
  result.milli = div.remainder;

  return result;
};

/**
 * Adds to a date to return a new date
 * @param {Date} startDate -
 * @param {Number} days -
 * @param {Number} hours -
 * @param {Number} minutes -
 * @param {Number} seconds -
 * @returns {Date} - new date
 */
export const addDate = (startDate, days, hours, minutes, seconds) => {
  const startTimeValue = checkDate(startDate);
  const newEndTimeEpoch = (startTimeValue ? startTimeValue.getTime() : new Date().getTime())
    + ((days) * TIME_OFFSET.DAYS)
    + ((hours) * TIME_OFFSET.HOURS)
    + ((minutes) * TIME_OFFSET.MINUTES)
    + ((seconds) * TIME_OFFSET.SECONDS);
  const newEndTime = new Date(newEndTimeEpoch);
  return newEndTime;
};

//-- Time Difference

/**
 * Result of clean divisions and remainders
 * @typedef {Object} DivisionRemainder
 * @property {Number} divisible - the whole number of times the number was divisible
 * @property {Number} remainder - the remainder after divisions
 */

/**
 * Divides a number into whole divisions and remainders
 * @param {Number} numerator - number to be divided
 * @param {Number} denominator - number to divide by
 * @returns {DivisionRemainder}
 */
export const divideRemainders = (numerator, denominator) => {
  let result = null;

  if (numerator >= 0) {
    result = ({
      divisible: Math.floor(numerator / denominator),
      remainder: numerator % denominator
    });
  } else {
    result = ({
      divisible: Math.ceil((numerator) / denominator),
      remainder: numerator % denominator
    });
  }

  //-- correct negative zero
  if (result.divisible === 0) {
    result.divisible = 0;
  }

  return result;
};

/**
 * Returns a zero padded number
 * @param {Number} num - number to be printed
 * @param {Number} length - length of the string
 * @returns {String} - padded number
 */
export const zeroFill = (num, length, padChar = '0') => {
  const isNegative = num < 0;
  return (isNegative ? '-' : '')
    // eslint-disable-next-line prefer-template
    + ('' + Math.abs(num)).padStart(length, padChar);
};

/**
 * Removes an item at a specific index in an array
 * @param {Array} targetArray - array to be cloned without a given index
 * @param {Number} index - index of the item to be removed
 * @returns {Array} - clone of targetArray, but without index
 */
export const removeArrayIndex = (targetArray, index) => {
  if (!targetArray || !Array.isArray(targetArray)) {
    return [];
  }

  if (index < 0 || index >= targetArray.length) {
    throw new Error(`Index:${index} is out of bounds for Array size:${targetArray.length}`);
  }

  return targetArray.slice(0, index).concat(targetArray.slice(index + 1, targetArray.length));
};

/**
 * Difference in time between two dates
 * @typedef {Object} Duration
 * @property {Number} days -
 * @property {Number} minutes -
 * @property {Number} hours -
 * @property {Number} seconds -
 * @property {Boolean} elapsed - whether the time has now elapsed
 */

/**
 * Calculates the duration between two dates
 * @param {Date} targetDate - the date to compare against
 * @param {Date} currentDate - the supposed current time
 * @returns {Duration} - the dime difference between the two
 */
export const timeDifference = (targetDate, currentDate = new Date()) => {
  const result = {
    days: 0,
    hours: 0,
    minutes: 0,
    seconds: 0
  };

  if (!targetDate) {
    return result;
  }

  let difference = 0;
  try {
    difference = targetDate.getTime() - currentDate.getTime();
  } catch (err) {
    console.error('error occurred', err);
  }
  // const difference = targetDate.getTime() - currentDate.getTime();
  result.elapsed = difference < 0;

  let div = divideRemainders(difference, TIME_OFFSET.DAYS);
  result.days = div.divisible;
  div = divideRemainders(div.remainder, TIME_OFFSET.HOURS);
  result.hours = div.divisible;
  div = divideRemainders(div.remainder, TIME_OFFSET.MINUTES);
  result.minutes = div.divisible;
  div = divideRemainders(div.remainder, TIME_OFFSET.SECONDS);
  result.seconds = div.divisible;

  return result;
};

/**
 * Calculates the duration of a length of time
 * @param {number} days -
 * @param {number} hours -
 * @param {number} minutes -
 * @param {number} seconds -
 * @returns {number} - the number of seconds in duration
 */
export const timeDuration = (days = 0, hours = 0, minutes = 0, seconds = 0) => {
  return 1000
    * (seconds
    + minutes * 60
    + hours * 60 * 60
    + days * 60 * 60 * 24);
};

/**
 * Milliseconds to Time
 * @param {number} milli - number of milliseconds
 * @returns {string} - HH:MM:SS.mmm formatted time
 */
export const millisecondsToTime = (milli) => {
  return new Date(milli).toISOString().slice(11, 23);
};

/**
 * Whether the reflow would be ascending for that timer mode or not
 * @param {String} mode - TIMER_MODE
 * @returns {Boolean} - true - ascending, false - descending
 */
export const isModeAscending = (mode) => {
  return mode !== TIMER_MODES.END_AT_TIME.value;
};

/**
 * The sum total duration for all the timers.
 * @param {Timer[]} timers - collection of timers
 * @returns {Number} - the sum of durations for all the tiemrs
 */
export const totalDuration = (timers) => {
  if (!timers || !Array.isArray(timers) || timers.length < 1) {
    return 0;
  }

  return timers.reduce((result, timer) => result + timer.duration, 0);
};

/**
 * Updates the start times of the timers
 * @param {Timer} timers - collection of timers
 * @param {String} mode - one of the TIMER_MODES
 * @param {Date} dateTime - the target time
 * @returns {Timer[]} - timers - updated in place
 */
export const reflowStartTimes = (timers, mode, dateTime) => {
  if (!timers || !Array.isArray(timers) || timers.length < 1) {
    return [];
  }

  const isAscending = isModeAscending(mode);

  if (isAscending) {
    let endTime = dateTime ? dateTime.getTime() : timers[0].startTimeMilli;
    let runningDuration = 0;

    //-- edit in place
    timers.forEach((timer) => {
      timer.startDate = serializeDate(new Date(endTime));
      timer.startTimeMilli = endTime;
      timer.endTimeMilli = timer.startTimeMilli + timer.duration;
      timer.durationStart = runningDuration;
      timer.durationEnd = timer.durationStart + timer.duration;

      endTime = timer.endTimeMilli;
      runningDuration = timer.durationEnd;
    });
  } else {
    const newTimers = timers.reduce((results, timer) => {
      results.unshift(timer);
      return results;
    }, []);
    let runningDuration = totalDuration(newTimers);
    let startTime = dateTime ? dateTime.getTime() : timers[0].endTimeMilli;
    newTimers.forEach((timer) => {
      timer.endTimeMilli = startTime;
      timer.startTimeMilli = startTime - timer.duration;
      timer.startDate = serializeDate(new Date(timer.startTimeMilli));
      timer.durationEnd = runningDuration;
      timer.durationStart = runningDuration - timer.duration;

      startTime = timer.startTimeMilli;
      runningDuration = timer.durationStart;
    });
  }

  return timers;
};

/**
 * Determien the duration we should use
 * @param {String} mode - TIMER_MODE
 * @param {Date} targetTime - the datetime of the start
 * @param {Number} fullDuration - the total duration of the entire set
 * @param {Date} currentTime - the current time
 * @param {Number} tickDuration - the current duration in milliseconds
 */
export const determineTimeElapsed = (mode, targetTime, fullDuration, currentTime, tickDuration) => {
  if (
    mode === TIMER_MODES.START_AT_TIME.value
    || mode === TIMER_MODES.END_AT_TIME.value
  ) {
    const timeBasedDuration = (mode === TIMER_MODES.START_AT_TIME.value)
      ? currentTime - targetTime
      : currentTime - (targetTime - fullDuration);
    if (timeBasedDuration < 0) {
      return 0;
    } else if (timeBasedDuration > fullDuration) {
      return fullDuration;
    }

    return timeBasedDuration;
  }

  //-- assume immediate
  return (tickDuration);
};

/**
 * Updates the progress amount in place for all the timers based on the time elapsed
 * @param {Number} timeElapsed - effective time elapsed
 * @param {Timer[]} timers - collection of timers
 * @returns {Timer[]} - timers passed - edited in place
 */
export const updateTimerProgress = (timeElapsed, timers) => {
  let timeElapsedRemainder = timeElapsed;
  if (timeElapsed < 0 || !timers) {
    return;
  }

  timers.forEach((timer) => {
    if (timeElapsedRemainder <= 0) {
      timer.progressPct = 0;
    } else if (timeElapsedRemainder > timer.duration) {
      timer.progressPct = 100;
      timeElapsedRemainder -= timer.duration;
    } else {
      // assume the timeElapsedRemainder > 0 && timeElapsedRemainder <= timer.duration
      timer.progressPct = ((timeElapsedRemainder / timer.duration) * 100);
      timeElapsedRemainder = 0;
    }
  });
};

/**
 * Splits a string with only the first occurrance
 * @param {String} str - string to be split
 * @param {String} char - character to be used as the delimiter
 * @returns {String[]} - [before, after]
 */
export const splitFirstOccurrance = (str, char) => {
  const charIndex = str.indexOf(char);
  if (charIndex < 0) {
    return [str, ''];
  } else {
    return [str.slice(0, charIndex), str.slice(charIndex + 1)];
  }
};

/**
 * Parses the url string
 * @param {String} searchStr - likely window.location.search
 * @returns {Object} - with the keys as properties
 */
export const parseSearchParams = (searchStr) => {
  return (searchStr || '')
    .split(/[?&]/)
    .filter((block) => block)
    .map((block) => splitFirstOccurrance(block, '='))
    .filter((keyValue) => Array.isArray(keyValue) && keyValue.length === 2)
    .reduce((result, [key, value]) => {
      result[key] = value;
      return result;
    }, {});
};

//-- thunks
// https://redux.js.org/tutorials/essentials/part-2-app-structure#writing-async-logic-with-thunks

//-- Middleware

export const timerMiddleware = (store) => (next) => (action) => {
  //-- ensure there is always at least a payload object
  if (!action.payload) {
    action.payload = {};
  }

  //-- this is only needed from the startTimer,
  //-- because it generates thunks outside of the normal flow
  if (action.type === 'timer/startTimer') {
    // console.log('middleware works!', store, next, action);

    const currentStore = selectorTimerSlice(store.getState());

    const {
      updateFrequency: updateFrequencyParam
    } = action.payload;

    const currentUpdateFrequency = updateFrequencyParam || currentStore.stopwatch.updateFrequency;

    const stopwatchState = store.getState().timer.stopwatch;
    const isIntervalRunning = selectorIsRunning(store.getState());
    action.payload.intervalPointer = stopwatchState.intervalPointer;

    //-- pass on the last tick
    action.payload.lastTick = new Date().getTime();

    if (!isIntervalRunning) {
      const newIntervalPointer = setInterval(() => {
        // console.log('middleware tick');
        const newTime = new Date().getTime();

        // store.dispatch({ type: 'timer/tick', payload: { newTime } });
        store.dispatch(timerSlice.actions.tick({ newTime }));
      }, currentUpdateFrequency);
      action.payload.intervalPointer = newIntervalPointer;

      //-- pass on the new update frequency
      action.payload.updateFrequency = currentUpdateFrequency;

      //-- pass on the last tick
      action.payload.lastTick = new Date().getTime();
    }
  } else if (action.type === 'timer/resetTimer') {
    // store.dispatch({ type: 'timer/stopTimer', payload: {} });
    store.dispatch(stopTimer());
  }

  return next(action);
};

//-- Store

/**
 * @typedef {Object} SerializedTimer
 * @property {string} id - the unique id of the timer
 * @property {string} name - the name of the timer
 * @property {number} hours -
 * @property {number} minutes -
 * @property {number} seconds -
 * @property {boolean} isActive -
 */

/**
 * @typedef {Object} TimerSettings
 * @property {string} mode - the mode of the timer slice
 * @property {Date} startDate - the date the timer will start (or null if now)
 */

/**
 * @typedef {Object} Stopwatch
 * @property {string} currentTime - ISO format of the current time
 * @property {string} tickDuration - the number of milliseconds elapsed
 * @property {string} tickDurationStr - the HH:MM:SS.mmm duration past the start time
 * @property {number} lastTick - the millisecond epoch of the current time
 * @property {number} updateFrequency - the milliseconds between updates
 * @property {number} intervalPointer - the pointer of the setTimeout
 */

/**
 * @typedef {Object} EditTimer
 * @property {Boolean} isOpen - whether the modal window is open
 * @property {String} uniqueKey - unique key for the timer
 * @property {String} timerName - name of the timer
 * @property {number} hours -
 * @property {number} minutes -
 * @property {number} seconds -
 */

/**
 * @typedef {Object} TimerSliceState
 * @property {TimerSettings} settings - slice for the settings
 * @property {Stopwatch} stopwatch - slice for starting and stopping the stopwatch
 * @property {EditTimer} editTimer - slice for the edit modal
 * @property {SerializedTimer[]} timers - collection of timers
 */

/**
 * @type {TimerSliceState}
 */
const initialState = {
  settings: {
    mode: TIMER_MODES.START_IMMEDIATELY.value,
    startDate: serializeDate(new Date()),
  },
  stopwatch: {
    experiencedDuration: 0,
    tickDuration: 0,
    tickDurationStr: millisecondsToTime(0),
    lastTick: new Date().getTime(),
    updateFrequency: DEFAULTS.UPDATE_FREQUENCY,
    intervalPointer: null
  },
  editTimer: {
    isOpen: false,
    uniqueKey: '',
    timerName: '',
    hours: 0,
    minutes: 0,
    seconds: 0
  },
  timers: TIMERS_EXAMPLE // TIMERS_INITIAL
};

// reflowStartTimes(initialState.timers);

const timerSlice = createSlice({
  name: 'timer',
  initialState,
  reducers: {
    /**
     * Sets the mode of the app
     * @param {*} state -
     * @param {*} action -
     */
    setMode: (state, action) => {
      state.settings.mode = action.payload;
    },

    /**
     * Time when the timer will start
     * (null)
     */
    setStartDate: (state, action) => {
      const newDate = deserializeDate(action.payload);
      const newDateSerialized = serializeDate(newDate);
      state.settings.startDate = newDateSerialized;

      if (state.timers && state.timers.length > 0) {
        state.timers[0].startDate = newDateSerialized;
        state.timers[0].startTimeMilli = newDate.getTime();
        reflowStartTimes(state.timers, state.settings.mode, newDate);
      }
    },

    /**
     * Starts the global timer
     */
    startTimer: (state, action) => {
      // console.log('reducer start timer');

      const {
        payload: {
          /** @type {number} */
          intervalPointer,
          /** @type {number} */
          updateFrequency,
          /** @type {number} */
          lastTick
        }
      } = action;

      if (updateFrequency) {
        state.stopwatch.updateFrequency = updateFrequency;
      }

      //-- passed from middleware
      state.stopwatch.lastTick = lastTick;
      state.stopwatch.intervalPointer = intervalPointer;
    },

    openEditTimer: (state, action) => {
      const { payload: { uniqueKey, timerName, hours, minutes, seconds } } = action;
      state.editTimer.isOpen = true;
      Object.assign(state.editTimer, { uniqueKey, timerName, hours, minutes, seconds });
    },

    closeEditTimer: (state, action) => {
      const { timerName, hours, minutes, seconds } = TIMER_DEFAULTS;
      state.editTimer.isOpen = false;
      Object.assign(state.editTimer, { timerName, hours, minutes, seconds });
    },

    /**
     * Tick for every update from the interval in set timer
     */
    tick: (state, action) => {
      console.log('TimerSlice tick');

      const newTime = (action?.payload?.newTime
        ? action.payload.newTime
        : new Date().getTime()
      );
      const timeDiff = newTime - state.stopwatch.lastTick;
      state.stopwatch.lastTick = newTime;
      state.stopwatch.currentTime = serializeDate(new Date(newTime));

      state.stopwatch.tickDuration += timeDiff;
      state.stopwatch.tickDurationStr = millisecondsToTime(
        state.stopwatch.tickDuration
      );

      const targetDate = deserializeDate(state.settings.startDate);
      const fullDuration = totalDuration(state.timers);
      const timeElapsed = determineTimeElapsed(
        state.settings.mode,
        targetDate.getTime(),
        fullDuration,
        newTime,
        state.stopwatch.tickDuration
      );

      updateTimerProgress(timeElapsed, state.timers);
    },

    /**
     * Stops the timer
     */
    stopTimer: (state, action) => {
      const baseState = !state.timer ? { timer: state } : state;
      if (!selectorIsRunning(baseState)) {
        console.error('timer is not currently running');
        return;
      }

      clearInterval(state.stopwatch.intervalPointer);
      state.stopwatch.intervalPointer = null;
    },

    /**
     * Clears the timer
     */
    resetTimer: (state, action) => {
      state.stopwatch.lastTick = new Date().getTime();
      state.stopwatch.tickDuration = 0;
      state.stopwatch.tickDurationStr = millisecondsToTime(0);

      updateTimerProgress(0, state.timers);
    },

    /**
     * Adds a timer to the list
     */
    addTimer: (state, action) => {
      const { uniqueKey, timerName, startDate, hours, minutes, seconds } = action.payload;
      const newTimer = createTimer(timerName, startDate, hours, minutes, seconds);
      const newTimerObj = newTimer; // newTimer.toStateObj();

      state.timers.push(newTimerObj);

      const newDate = deserializeDate(state.settings.startDate);
      reflowStartTimes(state.timers, state.settings.mode, newDate);
    },

    updateTimer: (state, action) => {
      const { uniqueKey, timerName, startDate, hours, minutes, seconds } = action.payload;
      const duration = timeDuration(0, hours, minutes, seconds);
      const foundTimer = !uniqueKey ? null
        : state.timers.find((t) => t.uniqueKey === uniqueKey);

      Object.assign(foundTimer, { timerName, startDate, hours, minutes, seconds, duration });

      const newDate = deserializeDate(state.settings.startDate);
      reflowStartTimes(state.timers, state.settings.mode, newDate);
    },

    moveTimerUp: (state, { payload: uniqueKey }) => {
      let foundTimer;
      let foundIndex;

      state.timers.forEach((timer, index) => {
        if (timer.uniqueKey === uniqueKey) {
          foundTimer = timer;
          foundIndex = index;
        }
      });

      if (
        !foundTimer
        || foundIndex < 1
      ) return state;

      const swapTimer = state.timers[foundIndex - 1];

      state.timers[foundIndex - 1] = foundTimer;
      state.timers[foundIndex] = swapTimer;

      reflowStartTimes(state.timers, state.settings.mode);

      return state;
    },

    moveTimerDown: (state, { payload: uniqueKey }) => {
      let foundTimer;
      let foundIndex;

      state.timers.forEach((timer, index) => {
        if (timer.uniqueKey === uniqueKey) {
          foundTimer = timer;
          foundIndex = index;
        }
      });

      if (
        !foundTimer
        || foundIndex > state.timers.length - 2
      ) return state;

      const swapTimer = state.timers[foundIndex + 1];

      state.timers[foundIndex + 1] = foundTimer;
      state.timers[foundIndex] = swapTimer;

      reflowStartTimes(state.timers, state.settings.mode);

      return state;
    },

    removeTimer: (state, { payload: uniqueKey }) => {
      /** @type {{ uniqueKey:String }} */
      // const { uniqueKey } = action.payload;

      state.timers = state.timers.filter((t) => t.uniqueKey !== uniqueKey);
      // removeArrayIndex(state.timer.timers, timerIndex);

      const newDate = deserializeDate(state.settings.startDate);
      reflowStartTimes(state.timers, state.settings.mode, newDate);
    },

    editTimer: (state, action) => {
      /** @type {{ uniqueKey:String, updatedTimer:Timer}} */
      // const { uniqueKey, updatedTimer } = action.payload;

      const uniqueKey = action.payload;

      /** @type {Timer} */
      const foundTimer = state.timers
        ?.find((currentTimer) => currentTimer.uniqueKey === uniqueKey);

      if (!foundTimer) {
        console.error(`Unable to find task:${uniqueKey}`);
        return;
      }

      // action.asyncDispatch(openEditTimer(foundTimer));
      const foundTimer2 = { ...foundTimer };
      action.asyncDispatch(openEditTimer(foundTimer2));
    },

    loadStoreFromURL: (state, action) => {
      // console.log('loading store from url');
      const params = parseSearchParams(window.location.search);
      const { state: newState } = params;

      if (newState) {
        // console.log('there is state');
        try {
          const newStateObj = JSON.parse(atob(newState));
          Object.assign(state, newStateObj);

          if (
            state.settings.mode === TIMER_MODES.START_AT_TIME.value
            || state.settings.mode === TIMER_MODES.END_AT_TIME.value
          ) {
            action.asyncDispatch(startTimer({}));
          }
        } catch (err) {
          console.error('State was found from url, but could not be parsed', newState);
        }
      }
    },

    shareURL: (state, action) => {
      const { settings, timers } = state;
      const sharableState = { settings, timers };
      const stateString = JSON.stringify(sharableState);
      const encodedState = btoa(stateString);

      window.history.replaceState(null, 'Meeting Timer', `?state=${encodedState}`);
      console.log('prepping to share the URL');
    },

    // #  #  #  #  #  #  #  #  #  #  #  #  #  #  #  #  #  #  #  #
    //-- old

    /**
     * Sets the current time
     */
    setTime: (state, action) => {
      state.stopwatch.lastTick = new Date(action.payload);
    }
  }
});

export const getStore = (property) => (store) => store.timer[property];

/** @returns {TimerSliceState} */
export const selectorTimerSlice = (store) => store.timer;

/** @returns {Date} */
export const selectorLastTickDate = (store) => {
  return new Date(store.timer.stopwatch.lastTick);
};

/**
 * Whether the stopwatch is currently running
 * @returns {Boolean} - true if running
 */
export const selectorIsRunning = (store) => {
  // eslint-disable-next-line no-unneeded-ternary
  return (store.timer.stopwatch.intervalPointer ? true : false);
};

/**
 * Whether there are any timers currently defined
 * @returns {Boolean} - whether timers is an empty list
 */
export const selectorHasTimers = (store) => (
  Array.isArray(store.timer.timers)
  && store.timer.timers.length > 0
);

/**
 * The edit timer form
 * @returns {EditTimer} - form info for the edit timer
 */
export const selectorEditTimerForm = (store) => (
  store.timer.editTimer
);

