/**
 * The placeholder for when the timer has or will start
 */
import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';

import {
  Box, Typography, Paper
} from '@mui/material';

import * as TimerSlice from '../data/TimerSlice';

/** @type {[import('../data/TimerSlice') */

const pluralizer = (str, number) => (number === 1 ? str : `${str}s`);

const TimerPlaceholder = ({
  theme, days, hours, minutes, seconds, elapsed
}) => {
  let textVariant = 'h6';
  if (days > 0) {
    textVariant = 'h7';
  } else {
    textVariant = 'h5';
  }
  return (
    <Paper>
      <Box p={3} textAlign="center">
        <Typography variant={textVariant} component="div">
          {elapsed
            ? (
              <span>Started &nbsp;</span>
            )
            : (
              <span>Will start in - &nbsp;</span>
            )}
          {days === 0 ? '' : (
            <span>
              {days} {pluralizer('day', days)}, &nbsp;
            </span>
          )}
          {hours === 0 ? '' : (
            <span>
              {hours} {pluralizer('hour', hours)}, &nbsp;
            </span>
          )}
          {minutes} {pluralizer('minute', minutes)}, &nbsp;
          {seconds} {pluralizer('second', seconds)}
          &nbsp;
          {elapsed
            ? (
              <span>ago</span>
            )
            : (
              <span />
            )}
        </Typography>
      </Box>
    </Paper>
  );
};

TimerPlaceholder.defaultProps = {
  theme: 'primary',
  days: 0,
  hours: 0,
  minutes: 0,
  seconds: 0,
  elapsed: false
};

TimerPlaceholder.propTypes = {
  /**
   * Description
   */
  theme: PropTypes.oneOf(['primary', 'secondary']),

  days: PropTypes.number,
  hours: PropTypes.number,
  minutes: PropTypes.number,
  seconds: PropTypes.number,

  elapsed: PropTypes.bool
};

export default TimerPlaceholder;

//-- wrapper around the timer placeholder to always update

const StartingCountdown = ({ theme, startTime }) => {
  const calculateTimeRemaining = () => TimerSlice.timeDifference(startTime, new Date());

  const timeRemainingState = React.useState(calculateTimeRemaining());

  /** @type {[TimerSlice.Duration, Function]} */
  const [timeRemaining, setTimeRemaining] = timeRemainingState;

  React.useEffect(() => {
    const timer = setTimeout(() => {
      const newDuration = calculateTimeRemaining();
      setTimeRemaining(newDuration);
    }, 1000);

    return () => {
      clearTimeout(timer);
    };
  });

  return (
    <TimerPlaceholder
      days={timeRemaining.days}
      hours={timeRemaining.hours}
      minutes={timeRemaining.minutes}
      seconds={timeRemaining.seconds}
      elapsed={timeRemaining.elapsed}
    />
  );
};

StartingCountdown.defaultProps = {
  theme: 'primary',
  startTime: new Date()
};

StartingCountdown.propTypes = {
  /**
   * Description
   */
  theme: PropTypes.oneOf(['primary', 'secondary']),

  startTime: PropTypes.oneOfType([
    PropTypes.instanceOf(Date),
    PropTypes.number,
    PropTypes.string
  ])
};

export { StartingCountdown };
