import React from 'react';
import PropTypes from 'prop-types';

import {
  Box,
  FormControl,
  Grid,
  Select,
  MenuItem,
  TextField
} from '@mui/material';

/** @type {[import('../data/TimerSlice') */

import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import DatePicker from '@mui/lab/DatePicker';
import { DateTimePicker } from '@mui/lab';

import * as TimerSlice from '../data/TimerSlice';

const { TIMER_MODES } = TimerSlice;

export const DEFAULTS = {
  mode: TIMER_MODES.START_IMMEDIATELY.value
};

// Date.prototype.toLocaleTimeString = Date.prototype.toLocaleString;

const MainSettingsPure = ({
  theme,
  timerMode,
  startDate,
  handleModeChanged,
  handleStartChanged
}) => {
  startDate = TimerSlice.checkDate(startDate);
  // debugger;
  const localizedTime = startDate ? TimerSlice.formatDate(startDate) : '';
  // const localizedTime = startDate ? startDate.toLocaleString() : '';

  const timeSelectorDetail = (timerMode && timerMode === TIMER_MODES.START_IMMEDIATELY.value)
    ? (
      <Box sx={{ paddingRight: '10px' }}>(Timer will begin when the &quot;Start&quot; button is pressed)</Box>
    )
    : (
      <DateTimePicker
        renderInput={(props) => <TextField variant="filled" sx={{ width: '100%' }} {...props} />}
        value={startDate}
        onChange={handleStartChanged}
      />
    );

  return (
    <Grid container spacing={2} alignItems="center" justifyContent="center" sx={{ paddingRight: '16px' }}>
      <Grid item xs={12} sm={6}>
        <Select
          id="input-timer-mode"
          sx={{ width: '100%' }}
          value={timerMode}
          onChange={handleModeChanged}
        >
          <MenuItem value={TIMER_MODES.START_IMMEDIATELY.value}>
            {TIMER_MODES.START_IMMEDIATELY.label}
          </MenuItem>
          <MenuItem value={TIMER_MODES.START_AT_TIME.value}>
            {TIMER_MODES.START_AT_TIME.label}
          </MenuItem>
          <MenuItem value={TIMER_MODES.END_AT_TIME.value}>
            {TIMER_MODES.END_AT_TIME.label}
          </MenuItem>
        </Select>
      </Grid>
      <Grid item xs={12} sm={6}>
        {timeSelectorDetail}
      </Grid>
    </Grid>
  );
};

MainSettingsPure.defaultProps = {
  theme: 'primary',
  timerMode: 'start-immediately',
  startDate: new Date(),
  handleStartChanged: () => {},
  handleModeChanged: () => {}
};

MainSettingsPure.propTypes = {
  /**
   * Description
   */
  theme: PropTypes.oneOf(['primary', 'secondary']),

  /*
  //-- @TODO - doesn't work?
  timerMode: PropTypes.oneOf([
    TIMER_MODES.START_IMMEDIATELY.value,
    TIMER_MODES.START_AT_TIME.value,
    TIMER_MODES.END_AT_TIME.value
  ]).isRequired,
  */
  timerMode: PropTypes.oneOf([
    'start-immediately',
    'start-at-time',
    'end-at-time'
  ]),

  startDate: PropTypes.oneOfType([
    PropTypes.instanceOf(Date),
    PropTypes.number,
    PropTypes.string
  ]),

  /**
   * When the mode has changed
   */
  handleModeChanged: PropTypes.func,

  handleStartChanged: PropTypes.func
};

export default MainSettingsPure;
