/**
 * Middleware that allows a reducer to dispatch another action
 * after the current action has completed.
 */
const asyncDispatchMiddleware = (store) => (next) => (action) => {
  let asyncActivityFinished = false;
  let actionQueue = [];

  function flushQueue() {
    actionQueue.forEach((a) => store.dispatch(a));
    actionQueue = [];
  }

  function asyncDispatch(asyncAction, payload) {
    let targetAction = asyncAction;
    if (typeof (asyncAction) === 'string' || asyncAction instanceof String) {
      targetAction = ({ type: asyncAction, payload });
    }
    actionQueue = actionQueue.concat([targetAction]);
  }

  const actionWithAsyncDispatch = ({ ...action, asyncDispatch });

  const res = next(actionWithAsyncDispatch);

  asyncActivityFinished = true;
  flushQueue();

  return res;
};
export default asyncDispatchMiddleware;
