// import * as TimerSlice from './TimerSlice';

import { v1 as uuidV1 } from 'uuid';

/**
 * Calculates the duration of a length of time
 * @TODO: merge with the TimerSlice duration
 * @param {number} days -
 * @param {number} hours -
 * @param {number} minutes -
 * @param {number} seconds -
 * @returns {number} - the number of seconds in duration
 */
export const timeDuration = (days = 0, hours = 0, minutes = 0, seconds = 0) => {
  return 1000
    * (seconds
    + minutes * 60
    + hours * 60 * 60
    + days * 60 * 60 * 24);
};

/**
 * Represents a specific timer
 */
class Timer {
  /**
   * Unique identifier for the timer
   */
  uniqueKey = '';

  /**
   * Name of the timer
   */
  timerName = '';

  /**
   * Time that the timer is expected to start
   */
  startDate = null;

  /** Milliseconds Epoch of the Start Time */
  startTimeMilli = 0;

  /** Millisecond Epoch of the End Time */
  endTimeMilli = 0;

  /**
   * The number of Hours the timer has
   * @type {Number}
   */
  hours = 0;

  /**
   * Minutes
   */
  minutes = 0;

  /**
   * Seconds
   */
  seconds = 0;

  /**
   * Duration
   */
  duration = 0;

  /** The milliseconds duration start of the entire timer */
  durationStart = 0;

  /** The milliseconds duration end of the entire timer */
  durationEnd = 0;

  /** The percentage progress on the timer */
  progressPct = 0;

  /**
   * Constructor
   */
  constructor(uniqueKey, timerName, startDate, hours, minutes, seconds, durationOffset = 0) {
    this.uniqueKey = uniqueKey;
    this.timerName = timerName;
    this.startDate = startDate;
    this.hours = hours;
    this.minutes = minutes;
    this.seconds = seconds;
    this.duration = timeDuration(0, hours, minutes, seconds);
    this.durationStart = durationOffset;
    this.durationEnd = durationOffset + this.duration;
    this.startTimeMilli = new Date(Date.parse(startDate)).getTime();
    this.endTimeMilli = this.startTimeMilli + this.duration;
  }

  /**
   * Merges a timer with another timer
   */
  merge(anotherTimer) {
    Object.assign(this, anotherTimer);
  }
}

export default Timer;

export const createTimer = (timerName, startDate, hours, minutes, seconds, durationOffset = 0) => {
  //-- https://www.npmjs.com/package/uuid
  const uniqueKey = uuidV1();
  const newTimer = new Timer(uniqueKey, timerName, startDate, hours, minutes, seconds, durationOffset);
  //-- @TODO: Timers aren't serializable. boo.
  return { ...newTimer };
};
