import React from 'react';
import PropTypes from 'prop-types';

import { makeStyles } from '@mui/styles';

import {
  Stack, Button
} from '@mui/material';

const actionStyles = makeStyles({
  actionButton: {
    width: 100,
    height: 100,
    borderRadius: 50,
    border: 0,
    color: '#FFF'
  },

  startBtn: {
    background: '#4DAE27',
    '&:hover': {
      background: '#60962B'
    }
  },

  stopBtn: {
    background: '#CA4949',
    '&:hover': {
      background: '#A62525'
    }
  },

  resetBtn: {
    background: '#777777',
    '&:hover': {
      background: '#666666'
    }
  }
});

const TimerActions = ({
  theme,
  isRunning,
  handleStartStop,
  handleReset
}) => {
  const classes = actionStyles();

  // const startStopStyles = ;
  // console.log(`startStopStyles:${startStopStyles}`);
  const startStopLabel = isRunning ? 'stop' : 'start';

  return (
    <Stack
      direction="row"
      alignItems="center"
      justifyContent="space-around"
      sx={{ width: '100%' }}
      spacing={1}
    >
      <Button
        variant="contained"
        className={`${classes.actionButton} ${isRunning ? classes.stopBtn : classes.startBtn}`}
        onClick={handleStartStop}
      >
        {startStopLabel}
      </Button>
      <Button
        variant="outlined"
        sx={{ width: 100, height: 100, borderRadius: 50 }}
        className={`${classes.actionButton} ${classes.resetBtn}`}
        onClick={handleReset}
      >
        Reset
      </Button>
    </Stack>
  );
};

TimerActions.defaultProps = {
  theme: 'primary',
  isRunning: false,
  handleStartStop: () => {},
  handleReset: () => {}
};

TimerActions.propTypes = {
  /**
   * Description
   */
  theme: PropTypes.oneOf(['primary', 'secondary']),

  isRunning: PropTypes.bool,

  handleStartStop: PropTypes.func,
  handleReset: PropTypes.func
};

export default TimerActions;
